body {
  font-family: "Work Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-image: url("/build/assets/bg5.svg");
  background-size: cover;
}

p {
  color: #000;
  font-size: 2rem;
}

.contain {
  width: 95%;
  max-width: 140rem;
  margin: 2rem auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Work Sans", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 2.6rem;
}

h2 {
  font-size: 2.4rem;
}

h3 {
  font-size: 2.2rem;
}

h4 {
  font-size: 1.8rem;
}

hr {
  opacity: 0.5;
}

button {
  all: unset;
  cursor: pointer;
}

.heading {
  text-align: center;
  font-size: 2.8rem;
  font-weight: 900;
}

.sub-heading {
  font-size: 2.2rem;
  font-weight: 900;
  text-align: left;
}

.little-heading {
  font-size: 2rem;
  font-weight: 900;
  text-align: left;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
  margin-top: 0;
  padding: 0 1rem 2rem 1rem;
}
@media (min-width: 768px) {
  .seccion {
    padding: 2rem 10rem;
    border-radius: 0.5rem;
  }
}

.error {
  color: #CB7766;
}

.main-contain {
  display: flex;
  flex-direction: column;
  margin: 0;
  width: 100%;
  background-color: #FFFFFF;
}
@media (min-width: 1024px) {
  .main-contain {
    margin: 2rem auto;
    border-radius: 0.5rem;
    width: 90%;
  }
}

.head {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.head .logo {
  max-width: 8rem;
}

.modal-menu-phone {
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4352941176);
  top: 0;
}
@media (min-width: 768px) {
  .modal-menu-phone {
    display: none;
  }
}
.modal-menu-phone .nav-phone {
  display: flex;
  flex-direction: column;
  width: 70%;
  padding: 2rem;
  height: 100%;
  background-color: #FFFFFF;
  gap: 2rem;
  margin-right: auto;
  transition: left 0.3s ease-out, opacity 0.5s linear;
  position: absolute;
  left: -80rem;
}
@media (min-width: 480px) {
  .modal-menu-phone .nav-phone {
    width: 60%;
  }
}
.modal-menu-phone .nav-phone button.close {
  all: unset;
  margin-left: auto;
  display: flex;
  justify-content: center;
  align-content: center;
  height: 3rem;
  width: 3rem;
  border-radius: 0.5rem;
  background-color: #CB7766;
  cursor: pointer;
  color: #FFFFFF;
  font-weight: 700;
}
.modal-menu-phone .nav-phone ul {
  width: 100%;
}
.modal-menu-phone .nav-phone ul li {
  display: block;
  list-style: none;
  padding: 0.5rem;
  background-color: #FFFFFF;
  font-size: 1.8rem;
  width: 100%;
  transition: all 0.3s ease-out;
}
.modal-menu-phone .nav-phone ul li a {
  display: flex;
  gap: 2rem;
  color: #000;
  font-weight: 700;
}
.modal-menu-phone .nav-phone ul li a img {
  height: 3.2rem;
  width: 3.2rem;
}
.modal-menu-phone .nav-phone ul li:hover {
  cursor: pointer;
  background-color: #F3F4F4;
}
.modal-menu-phone .display-nav {
  left: 0rem;
}

.display {
  display: flex;
  position: fixed;
}

.fecha {
  color: #39A900;
  font-weight: 700;
}

.message {
  margin-bottom: 1rem;
}

.note_1 {
  color: #39A900;
}

.note_2 {
  color: #FFCE40;
}

.field {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.field label {
  font-weight: 700;
}
.field input[type=text],
.field input[type=number],
.field input[type=email],
.field input[type=date],
.field input[type=file],
.field textarea,
.field select {
  width: 100%;
  outline: none;
  padding: 1rem;
  background-color: #f7f7f7;
  border: 2px solid #F3F4F4;
  border-radius: 0.5rem;
  transition: all 0.3s ease-out;
  text-align: center;
}
@media (min-width: 768px) {
  .field input[type=text],
  .field input[type=number],
  .field input[type=email],
  .field input[type=date],
  .field input[type=file],
  .field textarea,
  .field select {
    text-align: left;
  }
}
.field input[type=text]:focus,
.field input[type=number]:focus,
.field input[type=email]:focus,
.field input[type=date]:focus,
.field input[type=file]:focus,
.field textarea:focus,
.field select:focus {
  background-color: #FFFFFF;
  border: 2px solid #39A900;
}

.errors {
  margin-bottom: 1rem;
}

.heading.mt {
  margin-top: 3rem;
}

