header h1 {
  color: #FFFFFF;
  margin: 0;
}
header .splide .splide__track {
  height: 60rem;
}
header .splide .splide__track .splide__list .splide__slide {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
header .splide .splide__track .splide__list .splide__slide.slide-1 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)), url("/build/img/splide2.jpeg");
}
header .splide .splide__track .splide__list .splide__slide.slide-2 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)), url("/build/img/splide1.jpeg");
}
header .splide .splide__track .splide__list .splide__slide.slide-3 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)), url("/build/img/splide3.jpeg");
}

.nav {
  display: flex;
  align-items: center;
  position: absolute;
  width: 100%;
  padding: 0 3rem;
  margin-top: 2rem;
  z-index: 1;
}
.nav .nav__link {
  display: none;
  margin-left: auto;
}
.nav .nav__link .nav__item {
  list-style: none;
  font-size: 1.45rem;
}
.nav .nav__link .nav__item a {
  padding: 1.25rem 2.25rem;
  border-radius: 0.5rem;
  color: #FFFFFF;
  transition: background-color 0.3s ease-out;
}
.nav .nav__link .nav__item a:hover {
  background-color: rgba(255, 255, 255, 0.3882352941);
}
@media (min-width: 768px) {
  .nav .nav__link {
    display: flex;
  }
}

main.inicio {
  margin-top: 5rem;
}
main.inicio .concursos {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
main.inicio .concursos .concurso {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
}
main.inicio .concursos .concurso .concurso-informacion h1 {
  text-align: left;
}
main.inicio .concursos .concurso .concurso-informacion p {
  text-align: left;
}
main.inicio .concursos .concurso:nth-of-type(even) {
  direction: rtl;
}
@media (min-width: 1024px) {
  main.inicio .concursos .concurso {
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
main.inicio .cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3rem;
}
main.inicio .cards .card {
  display: flex;
  border-radius: 0.75rem;
  flex-direction: column;
  width: 35rem;
  padding: 3rem;
  height: 40rem;
  background-color: #FFFFFF;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
main.inicio .cards .card button {
  margin-top: auto;
  border-radius: 0.5rem;
}

section:nth-of-type(even) {
  background-color: #F3F4F4;
  padding: 2rem;
}

.footer {
  background-color: #1D293F;
}

.footer__title {
  font-weight: 300;
  font-size: 2rem;
  margin-bottom: 30px;
}

.footer__title, .footer__newsletter {
  color: #fff;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #fff;
  padding-bottom: 60px;
}

.nav__link--footer {
  display: flex;
  margin: 0;
  margin-right: 20px;
  flex-wrap: wrap;
}

.footer__inputs {
  margin-top: 10px;
  display: flex;
  overflow: hidden;
}

.footer__input {
  background-color: #fff;
  height: 50px;
  display: block;
  padding-left: 10px;
  border-radius: 6px;
  font-size: 1rem;
  outline: none;
  border: none;
  margin-right: 16px;
}

.footer__submit {
  margin-left: auto;
  display: inline-block;
  height: 50px;
  padding: 0 20px;
  background-color: #2091F9;
  border: none;
  font-size: 1rem;
  color: #fff;
  border-radius: 6px;
  cursor: pointer;
}